<template>
  <div class="p-page">
    <!-- <iframe src="https://www.nbou.cn/"></iframe> -->
    <!--  -->


  </div>
</template>

<script>
export default {
  data(){
    return{
      data:[],
    }
  },
  created(){

    this.getWeb()

  },
  methods:{

    getWeb() {
      console.log("success");
      uni.navigateTo({
        // 跳回小程序的路径
        url:
          "/pages/index/web" ,
        // 以下回调方法可以不要，如果需要调试的时候可以加上
        success: function (res) {
          console.log("success", res);

        },
        fail: function (res) {
          console.log("fail", res);
        },
        complete: function (res) {
          console.log("complete", res);
        },
      });
      this.$router.back();
    },

  }

}
</script>

<style lang="scss" scoped>

</style>



