<template>
  <div>
    <img :src="IMG + 'hr/icon/logoBg.png'" class="logoBg" />
    <div class="contentBox content">
      <!-- 标头 -->

      <img class="logo" :src="IMG + 'hr/icon/logo.png'" />
      <div class="companyName">深圳恒荣跨境电商物流有限公司</div>

      <div v-html="text"></div>
      <div
          v-if="item.image"
          :key="index"
          v-for="(item, index) in getContent('简介数据')"
          class="jsBg"
          :style="{ 'background-image': 'url(' + getUrl(item.image.url) + ')' }"
        >
          <span v-html="highlightText(item.name)"></span>
        </div>
        <!-- 专业化团队 -->
        <p class="fs-12 m-t-10">

专业化团队</p>

        <div class="tdBox" :style="{'background-image':'url('+getUrl(item2.image.url)+')'}" v-for="(item2,index2) in getContent('专业化团队')" :key="index2" >
          <div :class="index2 % 2 === 0 ? 'l-box':'r-box'" class="tdItem">
          <p class="tdName">{{ item2.name }}</p>
          <hr />
          <p class="tdDescribe">{{ item2.describe }}</p>
          </div>
        </div>
        <!-- 恒荣的优势 -->
        <p class="fs-12 m-t-10">恒荣的优势</p>
        <div class="ys-box">
          <div class="ys-item" v-for="(item,index) in getContent('恒荣的优势')" :key="index" >
            <el-image  class="ys-image" :src="item.image.url"></el-image>
            <p class="fs-14">{{ item.name }}</p>
            <p class="fs-10" style="color:#666666;text-align:justify">{{ item.describe }}</p>
        </div>



        </div>
    </div>

       <!-- 合作伙伴 -->
      <headerContent
      headerImg="hr/headerImage/hzhb.png"
      width="319px"
      height="42px"
     :isMx="false"
    >
      <template #content>
        <div class="hz-box">
          <van-image v-for="(item,index) in getContent('合作伙伴')" :key="index" :src="item.image.url"></van-image>

        </div>
      </template>
    </headerContent>


  </div>
</template>

<script>
  import { Toast } from "vant";
export default {

  data() {
    return {
      IMG: this.IMG,
      list: [],
      text: "",
      whList: [],
      show:false,
      phoneAndAddress:{
        name:'',
        pdf:'',
        name_en:'',
        describe:''
      }
    };
  },
  created() {
    this.getList();
  },

  methods: {
    highlightText(text) {
      // 使用正则表达式将数字（包括小数）和字母分开
      const regex = /(\d+\.\d+|\d+|[a-zA-Z]+|万|\＋)/g; // 匹配数字（包括小数）、字母、万和+

      return text.replace(regex, (match) => {
        if (/^\d+(\.\d+)?(万)?$|^\＋$/.test(match) || match === "万") {
          // 数字、小数、万、+ 显示为红色
          return `<span style="color:#E77D38;">${match}</span>`;
        } else {
          // 其他情况（如字母）显示为蓝色
          return `<span style="color: blue;">${match}</span>`;
        }
      });
    },
     // 电话
 getPhone() {
      window.location.href = "tel:" + this.phoneAndAddress.pdf;
    },
    // 地址
    getAddress() {
      Toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
      this.$router.push({
        path: "/map",
        query: {
          address: this.phoneAndAddress.describe,
          name: this.phoneAndAddress.name,
          isTap: true,
        },
      });
      Toast.clear;
    },
    getShow(res) {
      this.show = true;
      this.phoneAndAddress = res;
    },
    getUrl(str) {
      return "'" + str + "'";
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表内容" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
          this.text =
            this.getContent("简介内容") != []
              ? this.getContent("简介内容")[0]
                  .rich_text.replace(
                    /<p(.*?)>/,
                    "<p$1 style='width:100%;height:100%'>"
                  )
                  .replace(
                    /<video(.*?)>/,
                      "<video$1 style='width:100%;height:100%;border-radius:8px'>"
                  )
                  .replace(/<br\s*\/?>/gi, "")
                  .replace(
                    /(style=['"][^'"]*?)line-height:[^;'"]*?([^'"]*['"])/g,
                    (match, p1, p2) => {
                      return p1 + "line-height:0.6;" + p2; // 替换为新的line-height值
                    }
                  )
              : "";
        });
    },
  },
};
</script>

<style lang="scss" scoped>

.m-x-12 {
  margin: 0 12px;
}
.h-10 {
  height: 10px;
}
.m-x {
  margin: 12px 16px;
}
.logoBg {
  // position: fixed;
  top: 0;
  width: 100%;
  height: 206px;
}
.content {
  z-index: 1;
  margin: -40px 0 0 0 !important;
  padding: 0 28px 10px 28px;
  position: relative;
  border-start-start-radius: 20px;
}
.logo {
  width: 50px;
  height: 50px;
  margin: -50px 0 0 10px;
}
.box {
  width: 100%;
  height: 200px;
  background-color: beige;
  margin-top: 12px;
}
.name {
  font-size: 12px;
  color: #482F7D;
  margin: 8px 0;
}
.companyName {
  font-size: 13px;
  margin: 3px 0 8px 10px;
  color: #482F7D;
  font-weight: 500;
}
.introduction {
  width: 100%;
  height: 33px;
  padding: 8px 44px;
  background-size: 100% 100%;
  color: #333;
  font-size: 12px;
  margin-top: 7px;
}


.f-m{
  font-size: 12px;
  color: #333;
  margin-top: 10px;
  text-align: center;
}
.tdBox{
  width: 100%;
  height: 128px;
  background-size:100% 100%;
  border-radius:8px;
  margin-top:10px;
  .tdItem{

  }
  .r-box{
    height: 100%;
    float: right;
    width: 60%;
    border-radius: 0 8px 8px 0;
    background-color: rgba($color: #000000, $alpha: 0.6);
    padding:10px;
  }
  hr{
    margin:6px 0 ;
  }
  .l-box{
    height: 100%;
    float: left;
    width: 60%;
    border-radius: 8px 0  0 8px ;
    background-color: rgba($color: #000000, $alpha: 0.6);
    padding:10px;
  }
  .tdName{
    color:#fff;
    font-size:13px;
    font-weight: 500;
  }
  .tdDescribe{
    color:rgba($color: #fff, $alpha: 0.8);
    font-size:11px;

  }
}
.jsBg {
  width: 320px;
  height: 33px;
  background-size: 100% 100%;
  margin-top: 10px;
  font-size: 12px;
  font-weight: 500;
  padding: 10px 0 0 45px;
}
.ys-box{
  display: grid;
  grid-template-columns: repeat(
    2,
    50%
  ); // repeat(auto-fit, minmax(200px, 1fr));// /* 响应式列 */minmax(200px, 1fr): 每列的最小宽度为 200px，最大宽度为 1fr（平分剩余空间）。
  gap: 8px; /* 设置网格间距 */
  padding: 8px 0 ; /* 容器内边距 */
  .ys-item{
    background:rgba($color: #482F7D, $alpha: 0.05);
    border-radius:8px;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding:10px;
  }
  .ys-image{
    width: 21px;
    height: 21px;
    margin: 0 auto;
  }
}
.hz-box {
  display: grid;
  grid-template-columns: repeat(
    3,
    33%
  ); // repeat(auto-fit, minmax(200px, 1fr));// /* 响应式列 */minmax(200px, 1fr): 每列的最小宽度为 200px，最大宽度为 1fr（平分剩余空间）。
  gap: 8px; /* 设置网格间距 */
  padding: 8px; /* 容器内边距 */
}
</style>



