<template>
  <div class="p-page">
   <van-image :src="IMG+'nbzy/image/zsBg.png'" width="100%" height="160px" ></van-image>
   <div class="zsBg">
        <div @click="getTo(item.name,item.id)" v-for="(item,index) in getContent('招生就业')" class="zsItem" :key="index">
        <van-image :src="item.image.url" v-if="item.image" width="30px" height="30px"></van-image>
        <div class="name">{{ item.name }}</div>
        <div class="name_en">{{ item.name_en }}</div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
    };
  },
  created() {
    this.getList();
  },
  methods: {
    getTo(name,id){
      if(name=='政策解读' || name=="提前招生"){

      }else{
        this.$router.push({
        path: "/categorySolutionInfo",
        query: { id: id,title:name,isPage:false  },
      });
      }

    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表信息" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
        });
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
  },
};
</script>

<style lang="scss" scoped>
/deep/ .f-colum{
  justify-content: space-between;
}
.m-t-10{
  margin-top:10px;
}
.zsBg{
  display: grid;
  grid-template-columns: 49% 49%;
  column-gap:10px;
  row-gap: 10px;
  margin-top:10px;
  .zsItem{
    // width:155px;
    // height: 123px;
    border-radius: 8px;
    text-align: center;
    background-color: #fff;
    box-shadow: 0px 0px 8px 0px rgba(22,96,198,0.08);
    padding: 24px 0;
    .name{
      color: #333333;
      font-size: 14px;
      font-weight: 500;
      margin:6px 0 9px 0;
    }
    .name_en{
      font-size: 10px;
      color:#666666;

    }
}
}
</style>



