<template>
  <div>
    <img :src="IMG + 'dnad/image/logoBg.png'" class="logoBg" />
    <div class="contentBox content">
      <!-- 标头 -->

      <img class="logo" :src="IMG + 'dnad/image/logo.png'" />

      <div class="companyName">深圳市德宁安达供应链管理有限公司</div>
      <div
        class="textStyle"
        v-for="(item, index) in text.split(' ')"
        :key="index"
      >
        {{ item }}
      </div>
      <van-image :src="IMG + 'dnad/image/jjBg.png'" class="m-x-10"></van-image>
    </div>
    <div class="m-x">
      <!-- 安达承诺 -->
      <headerContent
        headerImg="dnad/headerImage/adcn.png"
        width="122px"
        height="21px"
      >
        <template #content>
          <div class="cnBox">
            <div
              v-for="(item, index) in getContent('安达承诺')"
              class="cnItem"
              :key="index"
            >
              <van-image
                :src="item.image.url"
                v-if="item.image"
                width="30px"
                height="30px"
                class="image"
              />

              <div class="name">{{ item.name }}</div>
              <div class="describe">{{ item.describe }}</div>
            </div>
          </div>
        </template>
      </headerContent>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      IMG: this.IMG,
      list: [],
      text: "德宁安达（深圳）国际物流供应链管理有限公司，总部设于深圳，2019年起转型做FBA头程物流，以国际物流供应链为核心，为我国跨境企业提供，海运，空运，海外仓，国际快递为一体的一站式服务，先后在深圳，广州，义乌设有多个仓库点，带领中国制造，安全、快速、到达世界的每一个角落。",
    };
  },
  created() {
    this.getList();
  },

  methods: {
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表信息" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.m-x {
  margin: 12px 16px;
}
.logoBg {
  // position: fixed;
  top: 0;
  width: 100%;
  height: 206px;
}
.content {
  z-index: 1;
  margin: -40px 0 0 0 !important;
  padding: 0 28px;
  position: relative;
  border-start-start-radius: 20px;
}
.logo {
  width: 50px;
  height: 50px;
  margin: -50px 0 0 10px;
}
.box {
  width: 100%;
  height: 200px;
  background-color: beige;
  margin-top: 12px;
}
.name {
  font-size: 12px;
  color: #355da8;
  margin: 8px 0;
}
.companyName {
  font-size: 13px;
  margin: 6px 0 8px 10px;
  color: #373692;
  font-weight: 500;
}

.cnBox {
  display: grid;
  grid-template-columns: 49% 49%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
  .cnItem {
    background-color: rgba($color: #04509e, $alpha: 0.05);
    border-radius: 8px;
    padding: 13px 12px;
    .image {
      display: flex;
      margin: auto;
    }
    .name {
      font-size: 14px;
      color: #333;
      margin: 5px 0;
      text-align: center;
    }
    .describe {
      font-size: 9px;
      color: #666666;
      text-align: center;
    }
  }
}
</style>



