<template>
  <div class="p-page">
    <!-- <iframe  id="iframe"  src="https://dynamiccard.oa00.com/main" frameborder="0"></iframe> -->
    <!-- <headerContent :isHeader="false" height="48px" width="100px">
      <template #content>

      </template>
    </headerContent> -->
     <!-- @click="getTo(item.name,item.id)" -->
     <div

          :style="{ 'background-image': 'url(' + getUrl(item.image.url) + ')' }"
          v-for="(item, index) in getContent('新闻资讯')"
          class="xwItem"
          :key="index"
        >
          <!-- <van-image :src="item.image.url" v-if="item.image" width="30px" height="30px"></van-image> -->
          <div class="name">{{ item.name }}</div>
        </div>
  </div>
</template>

<script>
export default {
  data() {
    return{
      list:[]
    }
  },
  created() {
    this.getList();
  },
  methods: {
    getUrl(str) {
      return "'" + str + "'";
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);

      return data ? data.solution_list : [];
    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表信息" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
#iframe {
  width: 100%;
  height: 100%;
}
.w-100 {
  width: 100%;
}
.h-100 {
  height: 95vh;
}
.xwItem {
  width: 100%;
  height: 160px;
  margin-top: 10px;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  .name {
    background-color: rgba($color: #000000, $alpha: 0.5);
    padding: 12px;
    border-radius: 0 0 8px 8px;
    text-align: center;
    color: rgba($color: #fff, $alpha: 0.8);
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
  }
}
</style>





