<template>
  <div>
    <img :src="IMG + 'cljc/image/logoBg.png'" class="logoBg" />
    <div class="contentBox content">
      <!-- 标头 -->

      <img class="logo" :src="IMG + 'cljc/image/logo.png'" />

      <div class="companyName">浙江晨龙锯床股份有限公司</div>
      <div
        class="textStyle"
        v-for="(item, index) in text.split(' ')"
        :key="index"
      >
        {{ item }}
      </div>
      <van-image :src="IMG + 'cljc/image/jjBg1.png'" class="m-x-10"></van-image>
      <div
        class="textStyle"
        v-for="(item, index) in text2.split(' ')"
        :key="index"
      >
        {{ item }}
      </div>
      <van-image :src="IMG + 'cljc/image/jjBg2.png'" class="m-x-10"></van-image>
    </div>
    <div class="m-x">
      <!-- 荣誉证书 -->
      <headerContent
        headerImg="cljc/headerImage/ryzs.png"
        width="117px"
        height="48px"
        id="cpzx"
      >
        <template #content>
          <div class="yyBox">
            <van-image
              width="155px"
              height="116px"
              v-if="item.image"
              :src="item.image.url"
              v-for="(item, index) in getContent('荣誉证书')"
              :key="index"
            ></van-image>
          </div>
        </template>
      </headerContent>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      IMG: this.IMG,
      list: [],
      text: "浙江晨龙锯床股份有限公司创办于2000年，是一家集高端智能锯切成套装备研发、生产、销售和服务于一体的国家级专精特新“小巨人”企业，拥有年产万台带锯床的综合生产能力，是国内智能锯切装备生产龙头企业。 公司是全国金属切削机床标准化技术委员会锯刨床分会秘书长单位、中国锻压协会下料装备委员会主任委员单位、中国机床工具工业协会锯床分会副理事长单位，是锯床行业首家被评为 “国家知识产权示范企业”、“全国文明单位”、“浙江省第一批制造业云上企业”，近年来相继被认定为“国家高新技术企业”、“浙江省隐形冠军”、“浙江省专精特新中小企业”、“浙江省知识产权示范企业”“浙江省级低碳绿色工厂”、“浙江省清洁生产企业”、“浙江省专利示范企业”、AAA“守合同重信用”企业和AAA级资信企业。“晨龙”牌商标被评为“中国驰名商标”、“浙江省著名商标”。 公司始终坚持“以人为本”理念，以“高精专特”为产品标准，设有锯床行业内首个国家级博士后科研工作站，省级锯切装备研发中心、浙江省晨龙智能锯切装备企业研究院、浙江省级高新技术企业研究开发中心、浙江省级企业技术中心等科研平台，公司先后承担国家级、省级课题9项（其中国家创新基金1项、国家火炬计划1项、国家重点新产品2项），省重大科技专项、主持或参与28项国家、行业标准的制修订工作，获授权专利235项（其中国内发明专利31项，国际PCT专利4项），获得国家、省级各类创新奖4项（其中高铁受电弓碳滑板自动化加工设备关键技术获评为19届中国专利优秀奖）。创造了高度2.5米的立锯、方料3米卧锯、曲轴专用锯床及西门子840D五轴联动技术的应用等多项“全国首台套”纪录,开发了全球首条高端地下装备轨排流水线、首条塔机下料智慧车间、首套高铁受电弓碳滑板自动化加工设备等，突破卡脖子技术，替代进口，成为国内锯切装备行业的领军企业。近年来，晨龙锯床产品多次获得国家重点新产品、浙江省科技进步二等奖、省装备制造业重点领域首（台）套、浙江制造精品等荣誉。",
      text2:
        "公司多年来注重与高校、科研院所的产学研合作，晨龙与浙江大学、浙江工业大学、杭州电子科技大学本着优势互补、互惠互利、相互支持、共同发展的原则，共同建立“浙大、浙工大、杭电一晨龙研究院”以及下属的智能制造装备中心、智能控制技术中心、自动化设计中心。在技术研发、人才培养、学术交流、成果转化，资源共享等方面开展卓有成效的全面合作，近年来进行产学研合作项目达30多项，合作金额逾2000万元。 公司注重服务质量，深受客户信赖。在全国各地设有100多个营销网点，300余位专业人员随时准备为客户提供售前、售后服务。近三年销售增长率均在20%以上，产品不仅在中国重汽、航空航天、上重、武钢、鞍钢、中原特钢、郑煤机以及三一重工等国家大型企业中使用，还远销美国、德国、意大利、印度、泰国、俄罗斯等20多个国家和地区。公司研制的智能锯切成套装备，已为中铁“高端地下装备制造项目轨排流水线”、中联重科“智能工厂制造执行系统（MES）”、山西建投等多家大型公司提供“机器换人整体解决方案”，并替代进口。 公司致力成为全球锯切技术驱动者，坚定不移推进企业数字化改革，围绕“产业大脑+未来工厂”的核心架构开展先试先行，将数字化人机交互控制技术深度植入产品，第二代AI智能控制系统，实现数据掌上互联，为锯床产业智能化技术探索前进的方向，实现从传统的制造企业向绿色、环保、节能、高效的智造企业转变。",
    };
  },
  created() {
    this.getList();
  },

  methods: {
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表信息" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.m-x {
  margin: 12px 16px;
}
.logoBg {
  // position: fixed;
  top: 0;
  width: 100%;
  height: 206px;
}
.content {
  z-index: 1;
  margin: -40px 0 0 0 !important;
  padding: 0 28px;
  position: relative;
  border-start-start-radius: 20px;
}
.logo {
  width: 50px;
  height: 50px;
  margin: -50px 0 0 10px;
}
.box {
  width: 100%;
  height: 200px;
  background-color: beige;
  margin-top: 12px;
}
.name {
  font-size: 12px;
  color: #355da8;
  margin: 8px 0;
}
.companyName {
  font-size: 13px;
  margin: 6px 0 8px 10px;
  color: #373692;
  font-weight: 500;
}

.yyBox {
  display: grid;
  grid-template-columns: 49% 49%;
  column-gap: 10px;
  row-gap: 10px;
  margin: 10px 0;
}
</style>



