<template>
  <div>
      <div class="contentBox d-flex justify-around" @click="toolShow=true">
        <span class="toolTitle">选择查询工具</span>
        <div class="toolName">
            {{ name }}
            <van-icon name="arrow"></van-icon>
        </div>
      </div>
      <div class="contentBox">
        <div class="name">{{ name }}</div>
        <div class="line"></div>
        <!-- <van-cell-group>
  <van-field v-model="inputText" placeholder="提示性文字" />

</van-cell-group> -->
        <input type="text" v-model="inputText" placeholder="请输入请输入关键词：中文名称/英文名称/五字码" />
        <div class="button" @click="get">查询</div>
      </div>
      <div class="contentBox" v-if="isResult">
          <div class="resultTitle">查询结果</div>
          <div class="result">暂无</div>
      </div>
      <van-popup v-model="toolShow"  close-icon-position="top-right" round :style="{ width:'50%', }">
      <div class="toolTitle" style="text-align: center;margin: 10px 0;" v-for="(item,index) in list" :key="index" @click="getName(item)">{{ item }}</div>
      </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: ['世界港口','世界港口1'],
      name:'世界港口',
      toolShow:false,
      isResult:false,
      inputText:''
    };
  },
  created() {
    // this.getList();
  },
  methods: {
    getName(item){
      this.name=item
      this.toolShow=false
    },
    get(){
      console.log(this.inputText)
      if(this.inputText){
        this.isResult=true
      }else{
        this.isResult=false
        this.$toast('请输入关键字')
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.justify-around{
  justify-content: space-between;
}
    .toolTitle{
      color:#333333;
      font-size:14px;

    }
    .toolName{
      width: 60%;
      color:#333333;
      font-size:14px;
      text-align: end;
    }
    .name{
      color: #3598DC;
      font-size: 14px;
      font-weight: 500;
      text-align: center;
    }
    .line{
      background-color: #3598DC;
      border-radius: 22px;
      width: 19px;
      height: 3px;
      margin: 5px auto;
    }
    .button{
      color: #fff;
      font-size: 14px;
      background-color: #3598DC;
      border-radius: 17px;
      margin: 15px auto;
      display: flex;
      justify-content: center;
      text-align: center;
      padding:5px 24px;
      width: fit-content;
    }
    .resultTitle{
      color:#333333;
      font-size:14px;
    }
    .result{
      color:#666666;
      font-size:12px;
      margin-top: 10px;
    }
  input{
    width: 100%;
    font-size: 12px;
    height: 100px;
    border: none;
    background-color: #f6f6f6;
    color: #999999;
    padding: 12px;
    border-radius: 12px;
  }
</style>



