<template>
  <div>
    <img :src="IMG + 'nbzy/image/logoBg.png'" class="logoBg" />
    <div class="contentBox content">
      <!-- 标头 -->

      <img class="logo" :src="IMG + 'nbzy/image/logo.png'" />
      <companyTitle
        title="宁波职业技术学院"
        fontSize="14px"
        color="#1660C6"
      ></companyTitle>
      <div
        class="text"
        v-html="item.rich_text"
        v-for="(item, index) in getContent('学校介绍详情')"
        :key="index"
      ></div>
      <div class="h-10"></div>
      <companyTitle
        title="学校领导"
        fontSize="13px"
        color="#333333"
      ></companyTitle>
      <div class="ldBox">
        <div class="ldItem" v-for="(item, index) in getContent('学校领导')" :key="index">
          <div class="describe">{{ item.name_en }}</div>
          <div class="name">{{ item.name }}</div>
        </div>
      </div>
      <companyTitle
        title="学校章程"
        fontSize="13px"
        color="#333333"
      ></companyTitle>
      <div class="zcBox ">
      <van-image :src="IMG+'nbzy/icon/xxzc.png'" width="12px" height="12px"></van-image>
      学校章程
      </div>
      <div class="h-10"></div>
    </div>
    <div class="m-x-12">
      <!-- 办学成果 -->
      <headerContent headerImg="nbzy/headerImage/bxcg.png"
    height="48px"
      width="169px"
      >
      <template #content>
        <div class="bxBg" v-for="(item,index) in getContent('办学成果')" :key="index" :style="{'backgroundImage':'url('+getUrl(item.image.url)+')'}">
        {{ item.name }}
        </div>

      </template>

    </headerContent>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      IMG: this.IMG,
      list: [],

    };
  },
  created() {
    this.getList();
  },

  methods: {
    getUrl(str) {
      return "'" + str + "'";
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表信息" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.m-x-12 {
  margin: 0 12px;
}
.h-10 {
  height: 10px;
}
.m-x {
  margin: 12px 16px;
}
.logoBg {
  // position: fixed;
  top: 0;
  width: 100%;
  height: 206px;
}
.content {
  z-index: 1;
  margin: -40px 0 0 0 !important;
  padding: 0 28px;
  position: relative;
  border-start-start-radius: 20px;
}
.logo {
  width: 50px;
  height: 50px;
  margin: -50px 0 0 10px;
}
.box {
  width: 100%;
  height: 200px;
  background-color: beige;
  margin-top: 12px;
}
.name {
  font-size: 12px;
  color: #024190;
  margin: 8px 0;
}
.companyName {
  font-size: 13px;
  margin: 6px 0 8px 10px;
  color: #024190;
  font-weight: 500;
}
.introduction {
  width: 100%;
  height: 33px;
  padding: 8px 44px;
  background-size: 100% 100%;
  color: #333;
  font-size: 12px;
  margin-top: 7px;
}

.ldBox{
  display: grid;
  grid-template-columns: 31% 31% 31%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
  .ldItem{
    background-color: #F6F6F6;
    border-radius: 8px;
    padding: 12px 6px;
    text-align: center;
    // width: 100px;
  }
  .name{
    color: #1660C6;
    font-size: 14px;
    font-weight: 500;
  }
  .describe{
    color: #333333;
    font-size: 12px;
    margin-bottom:4px;
    white-space: nowrap;
    letter-spacing: -1px;
    // height: 34px;
  }
}
.zcBox{
  background-color:#F6F6F6;
  width: 100%;
  text-align: center;
  padding:10px;
  border-radius: 10px;
  color: #333333;
  font-size: 14px;
}
.bxBg{
  width: 100%;
  height: 33px;
  border-radius: 8px;
  padding:9px 0 0 30px;
  color: #333333;
  font-size: 12px;
  margin-top:10px;
  background-size: 100% 100%;
}
.text{
  line-height: 19.86px;
  // text-indent: 2em;
  color: #333;
}
</style>



