<template>
  <div>
    <img :src="IMG + 'jrgj/image/logoBg.png'" class="logoBg" />
    <div class="contentBox content">
      <!-- 标头 -->

      <img class="logo" :src="IMG + 'jrgj/image/logo.png'" />
      <div class="companyName">义乌市巨润国际货运代理有限公司</div>
      <van-image :src="IMG + 'jrgj/image/jj1.png'" width="100%" height="160px"  />
      <div class="h-10"></div>
      <div v-for="item in text.split(' ')" class="textStyle">{{ item }}</div>
      <div class="h-10"></div>
      <van-image  class="m-auto" :src="IMG + 'jrgj/image/jj3.png'" width="100%" height="96.54px"  />
      <div class="h-10"></div>
    </div>
    <div class="m-x-12">
   <!-- 对点联运 -->
   <headerContent
      headerImg="jrgj/headerImage/ddly.png"
      width="221px"
      height="46px"
    >
      <template #content>
        <van-image :src="IMG+'jrgj/image/ddly.png'"></van-image>
      </template>
    </headerContent>

     <!-- 海运概况 -->
     <headerContent
      headerImg="jrgj/headerImage/hygk.png"
      width="290px"
      height="46px"
    >
      <template #content>
        <van-image :src="IMG+'jrgj/image/hygk1.png'"></van-image>
        <van-image :src="IMG+'jrgj/image/hygk2.png'"></van-image>

      </template>
    </headerContent>

  </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      IMG: this.IMG,
      list: [],
      text: "巨润国际货运代理有限公司成立于2010年，是一家主要经营国际进出口物流及相关业务的综合服务型企业。公司致力于“打造货运行业第一品牌”，以一体化精益物流、国际货运进出口代理、仓储、配送服务四大板块为主要业务发展方向，专注于中国出口至美国的海（空）运散货（整箱）双清门到门货运代理业务。 公司发展十五载，始终秉承'客户至上，合作共赢'的服务宗旨和'务实创新、诚信服务'的经营理念。总经营面积2万余平方米，以华东、华南、华北三大地区为依托，建立了四个国内转运中心、在美国洛杉矶、奥克兰、芝加哥、萨凡纳、休斯顿、细约等地拥有自营海外仓，员工数量100余人。目前为深圳坂田跨境电商物流协会常务理事单位、义乌跨境物流协会会员单位、甲必丹俱乐部金牌会员。 公司致力于开拓和发展国际海（空）运物流市场，拥有国家商务部批准的海运、空运双一级国际货运代理资质。公司的服务涵盖为客户提供专业的口岸报关、报检、仓储、代操作、代理海（空）运等口岸服务。为客户降低物流成本、优化速度、提升销量，提升全球卖家体验，通过专业的物流方案使客户在跨境电子商务领域提升竞争力。",
      whList:[
        {
          name:'价值观',
          list:'注重人才务实创新 客户至上合作共赢'
        },
        {
          name:'企业愿景',
          list:'立志打造货运行业第一品牌'
        },
        {
          name:'企业使命',
          list:'为跨境电商提供最优质的跨境物流服务'
        },
        {
          name:'企业理念',
          list:'诚信  务实  发展  共赢'
        },
      ]
    };
  },
  created() {
    this.getList();
  },

  methods: {
    getUrl(str) {
      return "'" + str + "'";
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
    getList() {
      this.$http
        .categorySolutionTypeList({ parent_name: "列表信息" })
        .then((item) => {
          console.log(item);
          this.list = item.data.list;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.m-x-12{
  margin:  0 12px;
}
.h-10 {
  height: 10px;
}
.m-x{
  margin: 12px 16px ;
}
.logoBg {
  // position: fixed;
  top: 0;
  width: 100%;
  height: 206px;
}
.content {
  z-index: 1;
  margin: -40px 0 0 0 !important;
  padding: 0 28px;
  position: relative;
  border-start-start-radius: 20px;
}
.logo {
  width: 50px;
  height: 50px;
  margin: -50px 0 0 10px;
}
.box {
  width: 100%;
  height: 200px;
  background-color: beige;
  margin-top: 12px;
}
.name{
  font-size: 12px;
  color: #024190;
  margin: 8px 0 ;
}
.companyName {
  font-size: 13px;
  margin: 6px 0 8px 10px;
  color: #024190;
  font-weight: 500;
}
.introduction {
  width: 100%;
  height: 33px;
  padding: 8px 44px;
  background-size: 100% 100%;
  color: #333;
  font-size: 12px;
  margin-top: 7px;
}
.fwBox {
  display: grid;
  grid-template-columns: 49% 49%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
}

.whBox{
  margin-top:10px;
  .whItem{
    color: #333333;
    font-size: 14px;
    font-weight: 700;
    margin-bottom:6px;
    .whImage{
      margin-right: 2px;
    }
  }
  .whItem2{
    display: flex;
      align-items: center;
      margin-left:20px;
    .describe{
      color: #666666;
      font-size: 12px;
      font-weight: 400;
      display: flex;
      align-items: center;
      margin-right:20px;

    .circular{
      width: 5px;
      height: 5px;
      background-color: #024190;
      border-radius: 3px;
      margin-right:6px;
    }
  }
  }

}
</style>



